<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title" style="text-align:center">
        <h2>库存测算</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu
          default-active="0-0-0"
          class="el-menu-vertical-demo"
          unique-opened
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="table">
        <div class="way-days">
          <el-button
            size="mini"
            :style="{
              'background-color': curIndex === 0 ? '#66b1ff' : '',
              color: curIndex === 0 ? '#fff' : ''
            }"
            @click="typeClick('钢木')"
            >钢木</el-button
          >
          <el-button
            size="mini"
            :style="{
              'background-color': curIndex === 1 ? '#66b1ff' : '',
              color: curIndex === 1 ? '#fff' : ''
            }"
            @click="typeClick('魔片')"
            >魔片</el-button
          >
          <span>装柜天数：{{ loadingDate }}</span>
          <span>海运周期：{{ shippingCycle }}</span>
          <span>补后可售天数：{{ buhouDays }}</span>
          <el-button type="primary" size="mini" @click="dateEdit"
            >编辑</el-button
          >
          <el-button size="mini" @click="checkedStockMonitor"
            >查看库存监测</el-button
          >
        </div>

        <el-table
          :data="stockCalculationList"
          border
          style="width: 100%"
          max-height="700"
          element-loading-text="拼命加载中呀"
          element-loading-spinner="el-icon-loading"
          show-summary
          v-loading="loading"
          :summary-method="getSummaries"
          ref="stockTable"
        >
          <el-table-column
            type="index"
            width="60"
            label="序号"
          ></el-table-column>
          <af-table-column prop="0" label="spu"></af-table-column>
          <af-table-column prop="1" label="sku"></af-table-column>
          <el-table-column prop="2" label="库存"></el-table-column>
          <el-table-column prop="3" label="在途数量"></el-table-column>
          <el-table-column prop="4" label="均单量">
            <template slot-scope="scope">
              <input
                type="number"
                v-model="scope.row[4]"
                @change="editChange(scope.$index)"
              />
            </template>
          </el-table-column>
          <el-table-column prop="5" label="预计售空日期"></el-table-column>
          <el-table-column prop="6" label="建议补货数量">
            <template slot-scope="scope">
              <input
                type="number"
                v-model="scope.row[6]"
                @change="editChange(scope.$index)"
              />
            </template>
          </el-table-column>
          <el-table-column prop="7" label="总体积/立方"></el-table-column>
          <el-table-column prop="8" label="总重量/kg"></el-table-column>
          <el-table-column prop="9" label="预计补后可售日期"></el-table-column>
        </el-table>
        <!-- <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="total"
        ></el-pagination>-->
      </div>
    </div>

    <!-- 日期编辑 -->
    <el-dialog
      :visible.sync="editDayDialogVisible"
      width="30%"
      center
      @close="editDayClose"
      ref="editDayRef"
    >
      <!-- <span>装柜天数：</span>
      <el-input v-model.number="editOnWayDays"></el-input>-->
      <el-form
        :model="editDayForm"
        :rules="formRules"
        ref="editDayRef"
        label-width="140px"
      >
        <el-form-item label="装柜天数" prop="loadingDate">
          <el-input v-model="editDayForm.loadingDate"></el-input>
        </el-form-item>
        <el-form-item label="海运周期" prop="shippingCycle">
          <el-input v-model="editDayForm.shippingCycle"></el-input>
        </el-form-item>
        <!-- <el-form-item label="补后售空日期" prop="buhouDate">
          <el-date-picker
            v-model="editDayForm.buhouDate"
            type="date"
            placeholder="选择日期"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            @change="pickerChange('日期')"
            :disabled="isDateDisabled"
          ></el-date-picker>
        </el-form-item>-->
        <el-form-item label="补后可售天数" prop="buhouDays">
          <el-input
            v-model="editDayForm.buhouDays"
            :disabled="isDaysDisabled"
            @change="pickerChange('天数')"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDayDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editDayConfirm">修 改</el-button>
      </span>
    </el-dialog>

    <!-- 下单 -->
    <el-dialog
      :visible.sync="xiadanDialogVisible"
      width="40%"
      center
      @close="xiadanClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form
        :model="xiadanForm"
        :rules="formRules"
        ref="xiadanRef"
        label-width="140px"
      >
        <el-form-item label="货柜号" prop="containNum">
          <el-input v-model="xiadanForm.containNum"></el-input>
        </el-form-item>
        <el-form-item label="预计发货日期" prop="fahuoDate">
          <el-date-picker
            v-model="xiadanForm.fahuoDate"
            type="date"
            placeholder="选择日期"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="仓库类型" prop="stockType">
          <el-input v-model="xiadanForm.stockType"></el-input>
        </el-form-item>-->
        <el-form-item label="仓库类型" prop="stockType">
          <el-select
            v-model="xiadanForm.stockType"
            clearable
            placeholder="请选择"
          >
            <el-option
              v-for="(item, index) in stockTypeList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="仓库编码" prop="stockCode">
          <el-input v-model="xiadanForm.stockCode"></el-input>
        </el-form-item>-->
        <el-form-item label="仓库名称" prop="stockName">
          <el-input v-model="xiadanForm.stockName"></el-input>
        </el-form-item>
        <!-- <el-form-item label="目的港" prop="mudigang">
          <el-input v-model="xiadanForm.mudigang"></el-input>
        </el-form-item>-->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiadanDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="xiadanConfirm">下 单</el-button>
      </span>
    </el-dialog>

    <!-- 查看库存监测 -->
    <el-dialog :visible.sync="monitorDialogVisible" width="96%" center>
      <el-table
        :data="stockMonitorList"
        border
        style="width: 100%"
        max-height="700"
        v-loading="tableLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(255, 255, 255, 0.8)"
      >
        <af-table-column
          prop="0"
          width="160"
          label="spu"
          fixed="left"
        ></af-table-column>
        <af-table-column
          prop="1"
          width="160"
          label="sku"
          fixed="left"
        ></af-table-column>
        <el-table-column prop="2" label="fba" fixed="left"></el-table-column>
        <el-table-column
          :prop="3 + index + ''"
          :label="item"
          v-for="(item, index) in containerList"
          :key="index"
        ></el-table-column>
        <el-table-column
          :prop="3 + containerList.length + index + ''"
          :label="item"
          v-for="(item, index) in tableFinalList"
          :key="index + 'a'"
          fixed="right"
          width="110"
        ></el-table-column>
      </el-table>
      <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="monitorDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="xiadanConfirm">下 单</el-button>
      </span>-->
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 选中按钮
      curIndex: 0,
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",

      // 选中的钢木还是魔片
      checkedType: "钢木",
      // 库存测算列表
      stockCalculationList: [
        // [1, 2, 3, 4, 5, 6, 1250, 66, 123456, 10],
        // [1, 2, 3, 4, 5, 6, 5, 8, 5556333, 10],
        // [1, 2, 3, 4, 5, 6, 233, 8, 9, 10],
        // [1, 2, 3, 4, 5, 6, 600, 8, 9, 10]
      ],

      // 编辑均单量和补货数量
      editForm: {
        // 均单量
        averageNum: "",
        // 预计补后可售天数
        saleDays: "",
        // sku
        sku: "",
        // 补货数量
        buhuoNum: ""
      },

      formRules: {
        averageNum: [
          { required: true, message: "请输入均单量", trigger: "blur" }
        ],
        buhuoNum: [
          { required: true, message: "请输入补货数量", trigger: "blur" }
        ],
        saleDays: [
          { required: true, message: "请输入可售天数", trigger: "blur" }
        ],
        containNum: [
          { required: true, message: "请输入货柜号", trigger: "blur" }
        ],
        stockType: [
          { required: true, message: "请输入仓库类型", trigger: "blur" }
        ],
        stockCode: [
          { required: true, message: "请输入仓库编码", trigger: "blur" }
        ],
        stockName: [
          { required: true, message: "请输入仓库名称", trigger: "blur" }
        ],
        mudigang: [
          { required: true, message: "请输入目的港", trigger: "blur" }
        ],
        fahuoDate: [
          {
            type: "string",
            required: true,
            message: "请选择发货日期",
            trigger: "change"
          }
        ],
        loadingDate: [
          { required: true, message: "请输入装柜天数", trigger: "blur" }
        ],
        shippingCycle: [
          { required: true, message: "请输入海运周期", trigger: "blur" }
        ]
      },
      isShowInput: [],
      // 编辑在途天数对话框
      editDayDialogVisible: false,
      editDayForm: {
        // 装柜天数
        loadingDate: "",
        // 海运周期
        shippingCycle: "",
        // 补后售空日期 日期或天数二选一
        buhouDate: "",
        buhouDays: ""
      },
      // 装柜天数
      loadingDate: "",
      // 海运周期
      shippingCycle: "",
      // 补后售空日期 日期或天数二选一
      buhouDate: "",
      buhouDays: "",
      isDateDisabled: false,
      isDaysDisabled: false,

      // 下单对话框
      xiadanDialogVisible: false,
      xiadanForm: {
        containNum: "",
        fahuoDate: "",
        stockType: "",
        stockCode: "",
        stockName: "",
        mudigang: ""
      },
      // 仓库类型
      stockTypeList: ["FBA", "海外仓"],
      loading: true,
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50,

      // 查看库存监测对话框
      monitorDialogVisible: false,
      stockMonitorList: [],
      containerList: [],
      tableFinalList: [
        "在途数量",
        "库存合计",
        "均单量",
        "预计售空日期",
        "提示"
      ],
      tableLoading: true,
      // 是否禁用下单按钮
      xiadanDisabled: false
    };
  },
  computed: {
    // 先用计算属性拿到日期
    seleteDate() {
      return this.editDayForm.buhouDate;
    },
    seleteDays() {
      return this.editDayForm.buhouDays;
    }
  },
  watch: {
    // 监听日期变化 如果日期被清除，应该要让月份可以选
    seleteDate(date) {
      if (date === null) {
        this.isDaysDisabled = false;
      }
    },
    seleteDays(days) {
      if (days === null) {
        this.isDateDisabled = false;
      }
    }
  },
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getStockMenuList().then(res => {
        console.log(res);
        // this.menuPlatFormList = res.data.data1;
        // this.menuCountryList = res.data.data2;
        // this.menuSiteList = res.data.data3;
        this.menuPlatFormList = res.data.platform_list;
        this.menuCountryList = res.data.country_list;
        this.menuSiteList = res.data.site_list;

        // 页面刷新 先默认展示第一个的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];

        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
        this.getDayInfo();
      });
    },
    // 页面加载获取相关日期
    getDayInfo() {
      this.$api
        .getDay(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedType
        )
        .then(res => {
          console.log(res);
          this.loadingDate = res.data.data[0];
          this.shippingCycle = res.data.data[1];
          this.buhouDays = res.data.data[2];
          this.buhouDate = res.data.data[3];
        });
    },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.stockCalculationList = [];
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      // 请求接口前先让其loading
      this.loading = true;
      // let status = 0;
      this.getDayInfo();
      this.$api
        .stockCalculation(platform, country, site, this.checkedType)
        .then(res => {
          // status = 1;
          console.log(res);
          this.stockCalculationList = this.$utils.deepClone(res.data.data);
          // 返回的数据最后一行可能是有12个数据，要把6，7删掉
          // 2021/1/26注释，返回数据已修正
          // if (
          //   this.stockCalculationList[this.stockCalculationList.length - 1]
          //     .length !== 9
          // ) {
          //   console.log("我不是9", this.stockCalculationList.length - 1);
          //   this.stockCalculationList[
          //     this.stockCalculationList.length - 1
          //   ].splice(6, 2);
          // }
          // 获取列表后取消loading
          this.loading = false;
          let translateList = [];
          const list = this.stockCalculationList;
          for (let i in list) {
            // 之前循环过的，这里需要跳出循环
            let isShow = list.slice(0, i).some(n => {
              return n[0] == list[i][0];
            });
            if (isShow == true) continue;
            // 创建一个空对象，保存spu和所有的补货数量，然后加进数组里面
            let obj = {};
            obj["spu"] = list[i][0];
            obj["buhuo"] = [];
            // 只拿取每个spu的补货数量，而不要索引，方便下边获取最小值
            let allBuhuo = [];
            // 用第二个变量去寻找与之相同的spu的补货数量，同时要记录索引
            for (let j in list) {
              if (list[i][0] === list[j][0]) {
                let buhuoObj = {};
                buhuoObj["index"] = j;
                buhuoObj["num"] = list[j][6];
                obj["buhuo"].push(buhuoObj);
                allBuhuo.push(list[j][6]);
              }
            }
            // 这个时候的obj就包含了一个spu中所有的补货数量了
            translateList.push(obj);
            // 拿到该spu中最少的补货数量
            allBuhuo = this.$utils.quicksort(allBuhuo);
            let min = allBuhuo[0];
            console.log(obj["buhuo"], typeof min);
            for (let item of obj["buhuo"]) {
              if (
                (item.num - min >= 500 && this.checkedType == "钢木") ||
                (item.num - min >= 200 && this.checkedType == "魔片")
              ) {
                let row = document.getElementsByClassName("el-table__row");
                this.$nextTick(() => {
                  console.log(
                    "打印row",
                    row,
                    item,
                    item.index,
                    row[item.index]
                  );

                  row[item.index].style.color = "red";

                  let num = parseInt(item.index) + 1;
                  let input = document.querySelectorAll(
                    ".el-table__body tr:nth-child(" + num + ") input"
                  );
                  for (let item of input) {
                    item.style.color = "red";
                  }
                  console.log(input);
                });
              }
            }
          }
        });
      // if (status == 0) {
      //   setTimeout(() => {
      //     this.loading = false;
      //   }, 1000);
      // }
    },
    // 钢木或魔片
    typeClick(type) {
      if (type === "钢木") {
        this.curIndex = 0;
      } else if (type === "魔片") {
        this.curIndex = 1;
      }
      this.checkedType = type;
      this.getDayInfo();
      this.thirdClick(
        this.checkedPlatform,
        this.checkedCountry,
        this.checkedSite
      );
    },
    // 自定义合计行
    getSummaries(param) {
      console.log("计算");
      const { columns, data } = param;

      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (
          !values.every(value => isNaN(value)) &&
          (index === 7 || index === 8 || index === 9)
        ) {
          // 计算和
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              // let a = Number(prev + curr).toFixed(2);
              return Number(prev + curr);
            } else {
              // let a = Number(prev).toFixed(2);
              return Number(prev);
            }
          }, 0);
          if (index === 7) {
            sums[index] = Math.round(sums[index]);
          }
          if (index === 8) {
            sums[index] = sums[index].toFixed(2);
          } else if (index === 9) {
            sums[index] = sums[index].toFixed(3);
          }
          // 体积是否超出67 nexttick要放在最外边
          this.$nextTick(() => {
            if (sums[8] > 67) {
              let td = document.querySelector(
                ".el-table__footer-wrapper tbody tr td:nth-child(9)"
              );
              td.style.background = "red";
              // td.setAttribute("class", "addColor");
              document.querySelector(".disabled").style.display = "none";
            } else {
              let td = document.querySelector(
                ".el-table__footer-wrapper tbody tr td:nth-child(9)"
              );
              td.style.background = "#F5F7FA";
            }
            // 美国 重量是否超出19500
            if (this.checkedCountry == "美国" && sums[9] > 19500) {
              // 注意nth-child是从1开始的
              let td = document.querySelector(
                ".el-table__footer-wrapper tbody tr td:nth-child(10)"
              );
              td.style.background = "red";
              document.querySelector(".disabled").style.display = "none";
            } else {
              let td = document.querySelector(
                ".el-table__footer-wrapper tbody tr td:nth-child(10)"
              );
              td.style.background = "#F5F7FA";
            }
            // 只要体积小于67 并且 重量小于19500或者不是美国 就显示下单
            if (
              (sums[8] < 67 || sums[8] === 67) &&
              (sums[9] < 19500 ||
                sums[9] === 19500 ||
                this.checkedCountry !== "美国")
            ) {
              document.querySelector(".disabled").style.display = "block";
            }
          });
        } else if (index === columns.length - 1) {
          sums[index] = (() => {
            // 这里只能用onclick，然后要在created那里挂在在window上
            let el = (
              <el-button
                class="disabled"
                type="primary"
                size="mini"
                onclick="goXiaDan()"
              >
                下单
              </el-button>
            );
            return el;
          })();
          return;
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    // 编辑日期相关
    dateEdit() {
      this.editDayForm.loadingDate = this.loadingDate;
      this.editDayForm.shippingCycle = this.shippingCycle;
      // this.editDayForm.buhouDate = this.buhouDate;
      this.editDayForm.buhouDays = this.buhouDays;
      this.editDayDialogVisible = true;
    },
    editDayClose() {
      this.$refs.editDayRef.resetFields();
    },
    // 编辑日期确认
    editDayConfirm() {
      this.$refs.editDayRef.validate(valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("platform", this.checkedPlatform);
        formData.append("country", this.checkedCountry);
        formData.append("site", this.checkedSite);
        formData.append("delivery_day", this.editDayForm.loadingDate);
        formData.append("shipping_cycle", this.editDayForm.shippingCycle);
        formData.append("product_type", this.checkedType);
        formData.append(
          "replenish_can_sell_day_number",
          this.editDayForm.buhouDays
        );
        // formData.append("replenish_can_sell_date", this.editDayForm.buhouDate);
        this.$api.editDay(formData).then(res => {
          console.log(res);
          this.$message.success("编辑成功");
          this.getDayInfo();
          this.editDayDialogVisible = false;
        });
      });
    },
    handleOpen(key) {
      // console.log(key);
    },
    pickerChange(val) {
      if (val == "日期") {
        this.isDaysDisabled = true;
      } else {
        this.isDateDisabled = true;
      }
    },

    // 下单
    xiadan() {
      this.xiadanDialogVisible = true;
    },
    xiadanClose() {
      this.$refs.xiadanRef.resetFields();
    },
    xiadanConfirm() {
      this.$refs.xiadanRef.validate(state => {
        if (!state) return;
        // 获取sku和补货数量的对象并push到数组中
        const list = this.stockCalculationList;
        let arr = [];
        let sku = "";
        let buhuo = "";
        for (let i = 0; i < list.length - 1; i++) {
          if (list[i][6] != 0) {
            sku = list[i][1];
            buhuo = list[i][6];
            let obj = {};
            obj["sku"] = sku;
            obj["num"] = buhuo;
            arr.push(obj);
          }
        }
        console.log(arr);

        let formData = new FormData();
        formData.append("platform", this.checkedPlatform);
        formData.append("country", this.checkedCountry);
        formData.append("site", this.checkedSite);
        formData.append("container", this.xiadanForm.containNum);
        // for (let item of arr) {
        //   formData.append("receive_data", JSON.stringify(item));
        // }
        formData.append("receive_data", JSON.stringify(arr));
        formData.append("delivery_date", this.xiadanForm.fahuoDate);
        formData.append("warehouse_type", this.xiadanForm.stockType);
        formData.append("warehouse_code", this.xiadanForm.stockCode);
        formData.append("warehouse_name", this.xiadanForm.stockName);
        formData.append("destination_port", this.xiadanForm.mudigang);
        this.$api.xiadan(formData).then(res => {
          console.log(res);
          this.$message.success("下单成功");
          this.xiadanDialogVisible = false;
        });
      });
    },
    // 编辑均单量和补货数量
    editChange(index) {
      console.log(index);
      this.editForm.averageNum = this.stockCalculationList[index][4];
      this.editForm.buhuoNum = this.stockCalculationList[index][6];
      this.editForm.sku = this.stockCalculationList[index][1];
      console.log(this.editForm);
      let formData = new FormData();
      formData.append("platform", this.checkedPlatform);
      formData.append("country", this.checkedCountry);
      formData.append("site", this.checkedSite);
      formData.append("sku", this.editForm.sku);
      formData.append("average_order", this.editForm.averageNum);
      formData.append("replenish_num", this.editForm.buhuoNum);
      this.$api.editDay(formData).then(res => {
        console.log(res);
        this.$message.success("编辑成功");
        // this.editDialogVisible = false;
        this.loading = true;
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // 分页
    handleCurrentChange(val) {
      console.log(val);
    },
    // 查看库存监测
    checkedStockMonitor() {
      this.tableLoading = true;
      this.$api
        .stockMonitor(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedType
        )
        .then(res => {
          console.log(res);
          this.tableLoading = false;
          this.containerList = res.data.header;
          this.stockMonitorList = res.data.data;
        });
      this.monitorDialogVisible = true;
    }
  },
  created() {
    this.getMenuList();
    // this.getCalculationList();
    // this.getDayInfo();
    window.goXiaDan = this.xiadan;
  },
  mounted() {},
  updated() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
  overflow: hidden;
  /* margin-top: 2vh; */
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  left: 18vw;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 24px;
  overflow: auto;
}
.right .el-table input {
  background: transparent;
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 4px 0;
}

.right .el-table input:hover {
  background: #f5f7fa;
}
.title {
  margin-top: 2vh;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.position {
  text-align: center;
}
.position-title {
  font-size: 1.4rem;
  font-weight: bold;
  margin-right: 10px;
}
.position-title span {
  font-weight: normal;
}
.button {
  margin: 10px 0 10px 0;
}
/* vue的深度选择器 */
/* .table >>> .el-input__inner {
  border: 0;
} */
.way-days {
  margin-bottom: 2vh;
}
/* .way-days .el-input {
  width: 12%;
} */
.table input {
  border: 0px;
  background-color: none;
  outline: none;
  width: 100%;
  font-size: 14px;
  color: #606266;
}
.table input:focus {
  outline: none;
}
/* 去掉input type=number的上下按钮 */
.table input::-webkit-outer-spin-button,
.table input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0;
}
/* .table input:hover {
  background-color: #f5f7fa;
} */
.right >>> .el-table__body tr:hover input {
  background-color: #f5f7fa !important;
}
.right >>> .el-table__body tr.get-red:hover {
  background-color: red;
}
#ipi-board >>> .el-dialog {
  margin-top: 10vh !important;
}
.addColor {
  background-color: red !important;
}
.width120 {
  width: 120px;
}
</style>
<style>
#ipi-board .el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: #f5f7fa;
}
#ipi-board .el-table--enable-row-hover .el-table__body tr > td {
  padding: 0;
  background-color: transparent;
}
#ipi-board .right .cell {
  width: 100%;
  height: 100%;
  padding: 0 0 0 10px;
}
</style>



